.cursor-container {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    pointer-events: none;
  }
  
  .cursor-line {
    position: absolute;
    width: 1px;
    height: 10px;
    background-color: black;
    opacity: 0.5;
    transform: rotate(45deg);
  }
      